<script>
  import api from 'src/packages/Api'
  import Loading from 'partials/Loading'
  import Uploader from 'view/components/uploader'
  import Relateds from 'view/components/relateds'
  import Multiselect from 'view/components/multiselect'

  export default {
    name: 'update-video',
    data () {
      return {
        video: {},

        id: 0,
        title: '',
        excerpt: '',        
        body: '',
        source: '',
        category: {},
        categories: [],
        related: [],

        loaded: false,
        apiUrl: 'videos/' + this.$route.params.id + "/upload"
      }
    },
    components: {
      Loading,
      Uploader,
      Relateds,
      Multiselect
    },
    
    async created () {
      try {
        const request = await api.get(`videos/${this.$route.params.id}`)
        const related = await api.post('related', request.data.related.data)

        this.video = request.data
        this.id = request.data.id
        this.title = request.data.title
        this.excerpt = request.data.excerpt
        this.body = request.data.body
        this.source = request.data.source
        this.category = request.data.category.data        

        this.related = related.data.map(r => {
          const selector = r.type === 'categories' ? 'name' : 'title'

          const rel = {
            selector,
            id: r.entity,
            type: {id: r.type, name: this.$t(`related.type_${r.type}`)}
          }

          return rel
        })

        this.loaded = true
      } catch(error) {
        this.$store.dispatch('addToastMessage',
          { text: this.$t(`errors.${error.data.status_code}`) }
        )
        this.$router.push({ name: 'videos' })
      }
    },
    methods: {
      findCategory (categories) {
        this.categories = categories
      },
      selectCategory (category) {
        this.category = category
      },
      getImage () {
        const image = this.video.image
          ? [this.video.image.data] 
          : []
          
        return image
      },

      reInitImage (response) {
        this.video = response.data
      },

      changeRelated ({old, changed}) {
        const relates = this.related.map(r => {
          if (r === old) {
            return changed
          } else {
            return r
          }
        })

        this.related = relates
      },

      deleteRelated (rel) {
        this.related = this.related.filter(r => r !== rel)
      },

      update () {
        this.$validator.validateAll().then((result) => {
          const relations = this.related
            .filter(r => !_.isEmpty(r.id))
            .map(r => {
              return {
                type: r.type.id,
                related_id: r.id.id
              }
            })

          if (result) {
            const data = {
              title: this.title,
              category_id: this.category.id,
              related: relations
            }

            api.put(`videos/${this.id}`, data).then(response => {
              this.$store.dispatch('addToastMessage',
                { text: this.$t('video.updated'), type: 'success' }
              )
              this.$router.push({ name: 'video', params: { id: response.data.id } })
            }).catch(error => {
              this.$store.dispatch('addToastMessage',
                { text: this.$t('errors.any'), error }
              )
            })
          }
        })
      }
    }
  }
</script>

<template>
  <loading v-if="!loaded"/>
  <div class="form-elements" v-else>
    <div class="row">
      <div class="col-md-12">
        <vuestic-widget :headerText="'menu.videos.update' | translate">

          <div class="row row-reverse">
            <div class="col-8">
              <div class="row row-reverse">
                <div class="col">
                  <div class="form-group with-icon-right"
                       :class="{'has-error': errors.has('title')}">
                    <div class="input-group">
                      <input
                        id="title"
                        name="title"
                        v-model="title"
                        v-validate="'required'"
                        required/>
                      <i
                        class="fa fa-exclamation-triangle icon-right input-icon"
                        v-show="errors.has('title')"></i>
                      <label class="control-label" for="title">{{'video.title'
                        | translate}}</label><i class="bar"></i>
                      <small v-show="errors.has('title')"
                             class="help text-danger">{{
                        errors.first('title')
                        }}
                      </small>
                    </div>
                  </div>
                </div>

                <div class="col">
                  <multiselect
                    :select="category"
                    :selects="categories"
                    :apiUrl="'categories/search'"
                    :rtl="true" 
                    @search="findCategory"
                    @select="selectCategory"
                  />
                </div>
              </div>
            </div>

            <div class="col-4">
              <Uploader
                :maxFiles="1"
                :apiUrl="apiUrl"
                :images="getImage"
                @onChange="reInitImage"
                :placeholder="'video.image' | translate"                
              />
            </div>
          </div>

          <relateds :relateds="related" @delRel="deleteRelated" @changeRel="changeRelated"/>

          <div class="row">
            <div class="col col text-center mt-4">
              <button class="btn btn-success"  @click="update">
                {{'buttons.update' | translate}}
              </button>
            </div>
          </div>
        </vuestic-widget>        
      </div>
    </div>
  </div>
</template>
